@import '../../../stylesheets/core/variables';

.design {
	// border: none;
	border-bottom: 1px solid #d8d8d8;
	margin: 20px 20px 0 20px;
}

.colors {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin: 31px 0 0 39px;
	// opacity: 1;
	// transition: opacity 0.5s ease;

	@media all and (min-width: 768px) {
		margin-left: 20px;
	}
}
.colors__title {
	font-family: $fontMain;
	font-size: 14px;
	color: $colorBrand08;
	text-transform: uppercase;
	margin-top: -9px;
}

.color-palette-1,
.color-palette-2,
.color-palette-3 {
	display: flex;
	flex-direction: row;
	align-items: flex-end;
}
// Paleta de color 1
.color-palette-1 {
	margin-bottom: 20px;
}
.input1 {
	margin-right: 10px;
	width: 17px;
	height: 17px;
	border-bottom-color: red;
}

.palette1-1 {
	width: 30px;
	height: 25px;
	background-color: $colorBrand05;
}
